var util = require("utils/util.js"); //获取应用实例
var weburl = getApp().globalData.weburl;
var shop_type = getApp().globalData.shop_type;
var navList = [{
  id: "is_recommend",
  title: "推荐",
  value: "1"
}];
var navList2 = [{
  id: "default",
  title: "最新"
}, {
  id: "hot",
  title: "人气"
}, {
  id: "price",
  title: "价格"
}];
import imageloader from "../../common/imageloader/imageloader";

export default {
  data() {
    return {
      title_name: '商城首页',
      title_logo: '/static/images/footer-icon-05.png',
      img_discount: '/static/images/discount.png',
      default_img: weburl + '/uploads/default_goods_image.png',
      activeIndex: 0,
      activeIndex2: 0,
      navList: navList,
      navList2: navList2,
      images: [],
      all_rows: 0,
      venuesItems_show: [],
	  venuesItems: [],
      show_max: 1,
      search_goodsname: null,
      keyword: '',
      page: 1,
      pagesize: 20,
      pageoffset: 0,
      indicatorDots: true,
      vertical: false,
      autoplay: true,
      interval: 3000,
      duration: 1000,
      loadingHidden: true,
      // loading
      is_goodslist_loading: false,
      msgList: [],
      hidden: true,
      scrollTop: 0,
      scrollHeight: 0,
	  old: {
	  	scrollTop: 0
	  },
      tab: 'is_recommend',
      tab_value: "1",
      tab2: 'default',
      updown: 0,
      //升序 降序
      shop_type: shop_type,
      //商家类型 1普通
      scrollLeft: 0,
      toView: 0,
      hiddenallclassify: false,
      animationData: "",
      floorstatus: false,
      loading_note: "",
      username: "",
      token: "",
      navlist_toView: "",
      navlist_title: "",
      userInfo: "",
      platform: "",
      windowHeight: "",
      windowWidth: "",
      dkheight: "",
      index: ""
    };
  },

  components: {
    imageloader
  },
  props: {},
  onLoad: function (options) {
    console.log('onLoad', options);
    var that = this;
    var username = options.username ? options.username : wx.getStorageSync('username');
    var token = options.token ? options.token : wx.getStorageSync('token');
    var navlist_toView = options.navlist ? options.navlist : 0;
    var navlist_title = options.navlist_title ? options.navlist_title : '';
    that.setData({
      username: username,
      token: token,
      navlist_toView: navlist_toView,
      navlist_title: navlist_title
    }); //that.setNavigation()
    //调用应用实例的方法获取全局数据

    wx.getSystemInfo({
      success: function (res) {
        that.setData({
          platform: res.platform,
          windowHeight: res.windowHeight,
          windowWidth: res.windowWidth,
          dkheight: res.windowHeight - 60
        });
      }
    });
    that.get_menubar();
  },
  onShow: function () {
    var that = this;
    var pages = getCurrentPages();

    if (pages.length > 1) {
      that.setData({
        title_logo: '/static/images/back.png'
      });
    }
  },
  onShareAppMessage: function () {
    return {
      title: '送心礼物',
      desc: '开启礼物电商时代，200万人都在用的礼物小程序！',
      path: '/pages/list/list?refername=' + username
    };
  },
  methods: {
    //定位数据  
    getleft: function (e) {
      var that = this;
	  var scrollLeft = that.scrollLeft
	  that.scrollLeft = scrollLeft + 10;
	  
    },
    // 打开全部子分类
    openAllTapTag: function (e) {
      var that = this;
      var hiddenallclassify = that.hiddenallclassify;
	  that.hiddenallclassify = !hiddenallclassify ;
    },
    goBack: function () {
      var pages = getCurrentPages();

      if (pages.length > 1) {
        wx.navigateBack({
          changed: true
        }); //返回上一页
      } else {
        wx.switchTab({
          url: '../hall/hall'
        });
      }
    },
    //事件处理函数
    swiperchange: function (e) {//console.log(e.detail.current)
    },

    

    // 点击获取对应分类的数据
    onTapTag: function (e) {
      var that = this; //var tab = e.currentTarget.id;
      var tab = e.currentTarget.dataset.id;
      var tab_value = e.currentTarget.dataset.value;
      var index = e.currentTarget.dataset.index;
      var search_goodsname = e.currentTarget.dataset.title;
      var navList = that.navList;
      var toView = index;
      var hiddenallclassify = that.hiddenallclassify;

      if (index > 2 && index < navList.length) {
        toView = index - 2;
      } else {
        toView = 0;
      }

      if (tab != 'search_goodsname') {
        search_goodsname = '';
      }

      that.setData({
        activeIndex: index,
        tab: tab,
        tab_value: tab_value,
        page: 1,
        search_goodsname: search_goodsname,
        toView: toView ? toView : 0,
        scrollTop: 0,
        venuesItems_show: [],
		venuesItems: [],
        pageoffset: 0,
		hiddenallclassify:!hiddenallclassify,
      });
      console.log('toView:' + that.toView,'hiddenallclassify:',that.hiddenallclassify);
      that.get_goods_list();
      if (hiddenallclassify == false) {
        that.openAllTapTag();
      }
      
    },
	
    onTapTag2: function (e) {
      var that = this;
      var id = e.currentTarget.id;
      var tab = e.currentTarget.dataset.tabid;
      var index = e.currentTarget.dataset.index;
      var updown = that.updown == 1 ? 0 : 1;
      that.setData({
        activeIndex2: index,
        tab2: tab,
        page: 1,
        updown: updown,
        venuesItems_show: [],
		venuesItems: [],
      });
      that.get_goods_list();
    },
    // 获取滚动条当前位置
    scrolltoupper: function (e) {
      var that = this;
	  var scrollTop = e.detail.scrollTop

      if ( scrollTop > 100) {
        this.setData({
          floorstatus: true
        });

        if (that.platform == 'ios') {//that.getMoreGoodsTapTag() //苹果手机渲染更快，多给记录
          //console.log('list scrolltoupper():', e.detail.scrollTop, that.data.platform)
        }
      } else {
        this.setData({
          floorstatus: false
        });
      } //console.log('list scrolltoupper():', e.detail.scrollTop)
		//console.log(' list scrolltoupper scrollLeft:',that.scrollLeft);
    },
	scroll: function(e) {
		var that = this
		var old_scrollTop = that.old.scrollTop
		var current_scrollTop = e.detail.scrollTop
		//console.log('list list:scroll:',e)
		that.old.scrollTop = current_scrollTop
		if(current_scrollTop > old_scrollTop +10) that.getMoreGoodsTapTag()
	},
	//回到顶部
	goTop: function (e) {
	  // 一键回到顶部
	  var that = this;
	  that.scrollTop = 0 ;
	  that.venuesItems_show = [] ;
	  that.page = 1 ;
	  that.pageoffset = 0 ;
	  console.log('goTop:', that.scrollTop);  
	  // 解决view层不同步的问题
	  that.scrollTop = that.old.scrollTop
	  that.$nextTick(function() {
	  	that.scrollTop = 0
	  });
	  that.get_goods_list();
	},
   
    searchTapTag: function (e) {
      var that = this;
      console.log('搜索关键字：' + that.search_goodsname);
      that.setData({
        venuesItems_show: []
      });
      that.get_goods_list();
    },
    getMoreGoodsTapTag: function () {
      var that = this;
      var page = that.page + 1;
      var all_rows = that.all_rows;
      var is_goodslist_loading = that.is_goodslist_loading;
      if (is_goodslist_loading) return;

      if (page > all_rows) {
        /*
        wx.showToast({
          title: '已经到底了~',
          icon: 'none',
          duration: 1000
        })
        */
        that.setData({
          loadingHidden: false,
          loading_note: '已经到底了'
        });
        setTimeout(function () {
          that.setData({
            loadingHidden: true
          });
        }, 1000);
        return;
      }

      that.setData({
        page: page
      });
      that.get_goods_list();
    },
    search_goodsnameTapTag: function (e) {
      var that = this;
      var keyword = e.detail.value;
      that.setData({
        keyword: keyword
      });
    },
    get_goods_list: function (event) {
      //venuesList
      var that = this;
      var page = that.page;
      var pagesize = that.pagesize;
      var pageoffset = that.pageoffset;
      var username = that.username;
      var token = that.token;
      var goods_type = that.tab;
      var goods_type_value = that.tab_value;
      var goods_sales = that.tab2;
      var updown = that.updown;
      var search_goodsname = that.search_goodsname;
      var keyword = that.keyword;
      var shop_type = that.shop_type; //var shape = 1

      var show_max = that.show_max;
      that.setData({
        is_goodslist_loading: true,
        loadingHidden: false
      });
      wx.request({
        url: weburl + '/api/client/get_goods_list',
        method: 'POST',
        data: {
          goods_type: goods_type,
          goods_type_value: goods_type_value,
          username: username,
          access_token: token,
          page: page,
          pagesize: pagesize,
          pageoffset: pageoffset,
          search_goodsname: search_goodsname,
          goods_sales: goods_sales,
          updown: updown,
          keyword: keyword,
          shop_type: shop_type // shape:shape
        },
        header: {
          'Content-Type': 'application/x-www-form-urlencoded',
          'Accept': 'application/json'
        },
        success: function (res) {
          var venuesItems_show = that.venuesItems_show;
          console.log('get_goods_list:', res.data, 'page:', page);
          var venuesItems_new = res.data.result;
          var all_rows = res.data.all_rows;
          var pageoffset = res.data.pageoffset;

          if (!venuesItems_new) {
			  /*
            wx.showToast({
              title: '没有搜到记录',
              icon: 'loading',
              duration: 1000
            });
			*/
            that.setData({
              venuesItems_show: [],
              all_rows: 0,
              is_goodslist_loading: false,
              keyword: ''
            });
            return;
          } 
		  var venuesItems = that.venuesItems
          if (venuesItems_new) {
            for (var i = 0; i < venuesItems_new.length; i++) {
              venuesItems_new[i]['short_name'] = venuesItems_new[i]['name'].substring(0, 10) + '...';

              if (!venuesItems_new[i]['act_info']) {
                venuesItems_new[i]['act_info'] = '';
              } else {//venuesItems_new[i]['act_info'] = venuesItems_new[i]['act_info'].substring(0, 10) + '...'
              }

              if (!venuesItems_new[i]['goods_tag']) {
                venuesItems_new[i]['goods_tag'] = '';
              } else {
                venuesItems_new[i]['goods_tag'] = venuesItems_new[i]['goods_tag'].substring(0, 10);
              }
              venuesItems_new[i]['image'] = venuesItems_new[i]['activity_image'] ? venuesItems_new[i]['activity_image'] : venuesItems_new[i]['image'];
            }

            //if (page > 1 && venuesItems_new) {
				 
			//} //向后合拼
           venuesItems = that.venuesItems.concat(venuesItems_new);
            //更新当前显示页信息
            //if (venuesItems_show.length >= show_max) {
            //  venuesItems_show.shift();
            //}
			that.venuesItems = venuesItems ;
			that.page = page ;
			that.all_rows = all_rows ;
			that.page = page ;
			that.pageoffset = pageoffset ;
			that.keyword = '' ;
			that.is_goodslist_loading = false ;
          }
        }
      });
    },
    get_menubar: function (event) {
      //获取菜单项
      var that = this;
      var navlist_toView = that.navlist_toView;
      var navlist_title = that.navlist_title;
      wx.request({
        url: weburl + '/api/client/get_menubar',
        method: 'POST',
        data: {
          menu_type: 1
        },
        header: {
          'Content-Type': 'application/x-www-form-urlencoded',
          'Accept': 'application/json'
        },
        success: function (res) {
          console.log('get_menubar:', res.data.result);
          var navList_new = res.data.result;

          if (!navList_new) {
            wx.showToast({
              title: '没有菜单项',
              icon: 'loading',
              duration: 1500
            });
            return;
          }

          for (var i = 0; i < navList_new.length; i++) {
            if (navList_new[i]['title'].indexOf(navlist_title) >= 0) {
              navlist_toView = i;
              break;
            }
          }
			that.navList = navList_new ;
			that.index = navlist_toView ;
			that.activeIndex = navlist_toView ;
			that.tab = navList_new[navlist_toView]['id'] ;
			that.tab_value = navList_new[navlist_toView]['value'] ;
			that.venuesItems_show = [] ;
			that.get_goods_list();
        }
      });
    },
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
    }
  }
};
